import React, { useState } from 'react'
import CountUp from 'react-countup';
import Cookies from 'universal-cookie'

import Nav from '../templates/header'
import Pagination from './pagination'
import axios from '../API.js'
const cookies = new Cookies();

export default class CollapsibleTable extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			rows: [
				this.createData('Perch MTurk Requester', 'Perch MTurk Requester', '23,983', '5', '7d ago', '', 'Click & Start Work', 1),
				this.createData('James Billing', 'James Billing', '22,255', '10', '7d ago', '', 'Click & Start Work', 1),
				this.createData('ThreesJS', 'ThreesJS', '21,145', '10', '7d ago', '', 'Click & Start Work', 1),
				this.createData('Scoutt', 'Scoutt', '19,234', '0.15', '7d ago', '', 'Click & Start Work', 1),
				this.createData('Scott Antipa', 'Scott Antipa', '15,875', '0.15', '7d ago', '', 'Click & Start Work', 1),
				this.createData('JC Sams', 'JC Sams', '14,756', 37, 4.3, 4.99, 1),
				this.createData('Xotto Byte', 'Xotto Byte', '12,334', 24, 6.0, 3.79, 1),
				this.createData('Cup whard', 'Cup whard', '10,235', 67, 4.3, 2.5, 1),
			],
			totalJobs1: 1025455,
			totalJobs: 1025455
		}
	}
	createData = (name, title, hits, reward, created, action, actionbutton, requesterid) => {
		return { name, title, hits, reward, created, action, actionbutton, requesterid };
	}

	componentDidMount() {
		var rowss = this.state
		let random2 = Math.round(Math.random() * 500, 0)

		axios.get("/Hits").then(res => {
			console.log(res)
			var data = res.data.data
			var hits = cookies.get('work1') ? cookies.get('work1') : 34589
			var hits1 = cookies.get('work2') ? cookies.get('work2') : 29574
			for (var i in data) {
				if (i === '0')
					rowss.rows.unshift(this.createData(data[i].RequesterName, data[i].Title, parseInt(hits1).toLocaleString(), data[i].Reward, data[i].created, '', 'Click & Start Work', data[i].requesterid))
				else rowss.rows.unshift(this.createData(data[i].RequesterName, data[i].Title, parseInt(hits).toLocaleString(), data[i].Reward, data[i].created, '', 'Click & Start Work', data[i].requesterid))
			}
			this.setState({ rowss })
			cookies.set('work1', hits - random2)
			cookies.set('work2', hits1 - random2)
		})

		let random1 = Math.round(Math.random() * 20, 0)
		this.setState({
			totalJobs: this.state.totalJobs + random1
		})
		setInterval(() => {
			this.setState({
				totalJobs1: this.state.totalJobs,
				totalJobs: this.state.totalJobs + random1,
			})
		}, 10000)
	}

	render() {
		return (<>
			<Nav mainactive="hits" subactive="allhits" />

			<div className="p-4">
				<div className="row text-center">
					<div className="col-6">
						<h2>AVAILABLE WORKS</h2>
						<h3><CountUp start={this.state.totalJobs1} end={this.state.totalJobs} duration={20} /></h3>
					</div>
					<div className="col-6">
						<h2>TOTAL WORKERS ONLINE</h2>
						<h3><i className="fa fa-circle text-success" aria-hidden="true"></i>{'  '}<CountUp start={this.state.totalJobs1 - 959990} end={this.state.totalJobs - 959990} duration={20} /></h3>
					</div>
				</div>
				<Pagination key={1} rows={this.state.rows} {...this.props} />

			</div>
		</>)

	}
}

