import React from 'react'
import Cookies from 'universal-cookie'
import { Card, Button, CardHeader, CardBody } from 'reactstrap';

import { connect } from 'react-redux'

import axios from '../API.js'
import { validateEmail, clear, validateFields, customValidation } from '../common.js'

const cookies = new Cookies();

class App extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			id: '1', firstamt: '', secondamt: '', isError: false,
			error: { firstamt: '', secondamt: '' }
		}
	}
	onChange = (e) => {
		let data = this.state
		data[e.target.name] = e.target.value
		this.setState({ data })
		if (this.state.isError) {
			this.onValidation(data)
		}
	}
	onValidation = (datas) => {
		const emptyFields = [
			{ key: 'firstamt', errVal: 'Enter First Amount' },
			{ key: 'secondamt', errVal: 'Enter Second Amount' },
		]

		let input = datas === undefined ? this.state : datas
		const data = validateFields(input, emptyFields)
		this.setState({ data })
		return data.isError
	}
	onSubmit = () => {
		const data = this.state
		// if (!this.onValidation()) 
		{
			this.props.history.push('/allhits')
		}
	}

	render() {
		let res;
		res = (
			<>
				<div className="row text-center centerscreen">
					<h1 className="tm-site-name logocolor">Freelance <span style={{ color: 'black' }}>Typers</span></h1>
					<div className="col-md-4 col-12"></div>
					<div className="col-md-4 col-12">
						<Card>
							<CardBody className="text-left padding-20 font-weight-bold">

								<h2 className="text-center">Congrats! Your bank account details submitted successfully!</h2>
								<br />
								<ul style={{ paddingLeft: '1em' }}>
									<li style={{ listStyle: 'disc' }}>
										Freelance Typers will transfer two deposit amounts to your bank account within 1-2 business days.
									</li>
									<li style={{ listStyle: 'disc' }}>
										You look for the deposits  amounts on your bank statement and enter the deposit amounts on the freelance typers website given below box.
									</li>
									<li style={{ listStyle: 'disc' }}>
										After you have entered the correct deposit amounts, you can transfer your earnings direct to your bank account.
									</li>
								</ul>
								<br />
								<div className="form-group">
									<label>First Amount</label>
									<input name="firstamt" type="text" className="form-control" onChange={this.onChange} value={this.state.firstamt} />
									{this.state.error.firstamt}
								</div>
								<div className="form-group">
									<label>Second Amount</label>
									<input name="secondamt" type="text" className="form-control" onChange={this.onChange} value={this.state.secondamt} />
									{this.state.error.secondamt}
								</div>

								<div className="text-right">
									<button className="button col-12" onClick={this.onSubmit}>Submit</button>
								</div>
							</CardBody>
						</Card>
					</div>
					<div className="col-md-4 col-12"></div>
				</div>
			</>
		)
		return res;
	}
}

const mapStateToProps = (state) => ({ common: state.common })

export default connect(mapStateToProps)(App);